<template>
    <div>
        <h3 class="mb-base">Настройки</h3>


        <div class="vx-row">
            <div class="vx-col w-full lg:w-1/2 mb-base">
                <vx-card title="Общие" class="mb-base">
                    <div style="display:flex; justify-content: space-between;">
                        <p>enablePyDriver</p>
                        <vs-switch color="success" v-model="enablePyDriver"/>
                    </div>
                </vx-card>

                <vx-card title="Безопасность">
                    <div style="display:flex; justify-content: space-between;">
                        <p>Подпись запросов</p>
                        <vs-switch color="success" v-model="isSyng" disabled="true"/>
                    </div>
                    <vs-divider ></vs-divider>

                    <div class="mt-6">
                        <h5 class="mb-3" >Список токенов:</h5>
                        <div style="color: #cbcbcb">Список пуст</div>
                    </div>
                </vx-card>
            </div>
            <div class="vx-col w-full lg:w-1/2 mb-base">
                <vx-card title="Подсветка модулей">
                     <div class="mb-5" >
                         <h5>Базовый цвет</h5>
                         <swatches @input="setBaseColor" v-model="color_base" shapes="circles" show-border :colors="['#FFFFFF', '#FF0000', '#00FF00', '#0000FF', '#00FFFF', '#FFFF00', '#FF00FF']" inline/>
                     </div>

                    <div >
                        <h5>Цвет ошибки</h5>
                        <swatches @input="setErrorColor" v-model="color_error" shapes="circles" show-border :colors="['#FFFFFF', '#FF0000', '#00FF00', '#0000FF', '#00FFFF', '#FFFF00', '#FF00FF']" inline/>
                     </div>



                </vx-card>
            </div>
        </div>

    </div>
</template>

<script>
    import Swatches from 'vue-swatches'
    import "vue-swatches/dist/vue-swatches.min.css"

    export default {
        components: {
            Swatches
        },
        watch: {
            enablePyDriver(state){
                this.setEnablePyDriver(state)
            }
        },
        data(){
            return {
                isSyng: false,
                isLoading: true,
                color_base: '#00FFFF',
                color_error: '#FF0000',
                enablePyDriver: true
            }
        },
        created(){
            this.getSettings()
        },
        methods: {
            async setEnablePyDriver(enable){
                try{
                    await this.$http.post('/settings/py-driver', {enable});
                    this.$vs.notify({
                        color: 'success',
                        title: 'Успешно',
                        text: 'Данные сохранены'
                    });
                }catch (e) {
                    if(e.response.status === 403){
                        this.$vs.notify({
                            color: 'warning',
                            title: `Ошибка`,
                            text: e.response.data.message,
                        });
                    }else{
                        this.$vs.dialog({
                            type: 'confirm',
                            color: 'warning',
                            title: `Ошибка`,
                            text: 'Не удалось сохранить данные, повторите попытку',
                            accept: this.setEnablePyDriver,
                            acceptText: "Повторить",
                            cancelText: "Отменить"
                        })
                    }
                }
            },
            async getSettings(){
                try{
                    let response = await this.$http.get('/settings');

                    this.color_base = this.convert2HEX(response.data.base_color);
                    this.color_error = this.convert2HEX(response.data.error_color);
                    this.enablePyDriver = response.data.enablePyDriver;
                    this.isLoading = false
                }catch (e) {
                    console.log(e)
                }
            },
            async setBaseColor(){
                try{
                    await this.$http.post('/settings/base-color', {color: this.convert2RGBA(this.color_base)});
                    this.$vs.notify({
                        color: 'success',
                        title: 'Успешно',
                        text: 'Данные сохранены'
                    });
                }catch (e) {
                    if(e.response.status === 403){
                        this.$vs.notify({
                            color: 'warning',
                            title: `Ошибка`,
                            text: e.response.data.message,
                        });
                    }else{
                        this.$vs.dialog({
                            type: 'confirm',
                            color: 'warning',
                            title: `Ошибка`,
                            text: 'Не удалось сохранить данные, повторите попытку',
                            accept: this.setBaseColor,
                            acceptText: "Повторить",
                            cancelText: "Отменить"
                        })
                    }
                }
            },
            async setErrorColor(){
                try{
                    await this.$http.post('/settings/error-color', {color: this.convert2RGBA(this.color_error)});
                    this.$vs.notify({
                        color: 'success',
                        title: 'Успешно',
                        text: 'Данные сохранены'
                    });
                }catch (e) {
                    if(e.response.status === 403){
                        this.$vs.notify({
                            color: 'warning',
                            title: `Ошибка`,
                            text: e.response.data.message,
                        });
                    }else{
                        this.$vs.dialog({
                            type: 'confirm',
                            color: 'warning',
                            title: `Ошибка`,
                            text: 'Не удалось сохранить данные, повторите попытку',
                            accept: this.setBaseColor,
                            acceptText: "Повторить",
                            cancelText: "Отменить"
                        })
                    }
                }
            },
            convert2RGBA(hex){
                hex = hex.replace('#','');
                let res = {
                    r: parseInt(hex.substring(0, 2), 16),
                    g: parseInt(hex.substring(2, 4), 16),
                    b: parseInt(hex.substring(4, 6), 16),
                };
                return res;
            },
            convert2HEX({r, g, b}) {
                return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
            }
        }
    }
</script>

